footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    min-height: 100px;
    position: relative;
    margin-top: 50px;
}
footer > h1{
    position: absolute;
    left: 80px;
}
#footer-socials{
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(5, 1fr);
    position: absolute;
    right: 80px;
}