header{
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: var(--background-color);
    justify-content: center;
    min-height: 100px;
    width: 100%;
    position: relative;
}
header > h1{
    position: absolute;
    left: 80px;
}
#header-navbar{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 25%;
}
#header-navbar > a{
    text-align: center;
    text-decoration: none;
    color: var(--accent-color);
}
#header-options{
    position: absolute;
    right: 80px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
}