:root{
    --background-color: #FFFFFF;
    --accent-color: #14203E;
    --other-color: #F6F0E8;
}
@font-face{
    font-family: 'ibm';
    src: url('../assets/fonts/IBMPlexSans-Regular.ttf');
    font-weight: normal;
}
@font-face{
    font-family: 'ibm';
    src: url('../assets/fonts/IBMPlexSans-Bold.ttf');
    font-weight: bold;
}

h1, h2, h3 {
    font-family: 'ibm';
    font-weight: bold;
    color: var(--accent-color);
}
p, a, input{
    font-family: 'ibm';
    font-weight: normal;
    color: var(--accent-color);
}

/* Buttons */
.btn{
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    border-radius: 10px;
    color: var(--accent-color);
    background-color: var(--other-color);
    transition: filter 0.5s;
}
.btn:hover{
    filter: brightness(1.5);
}
.btn.special{
    background-color: var(--accent-color);
    color: var(--background-color);
}