#hero-banner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 75vh;
    background-color: var(--other-color);
    width: 100%;
}
#hero-banner-text{
    width: 38%;
    text-align: center;
}
#hero-banner-text > h1{
    font-size: 40px;
}
#hero-banner-text > p{
    font-size: 18px;
    margin-bottom: 50px;
}
#hero-banner-text > a{
    font-size: 20px;
    padding: 15px 30px;
}

#blog-header{
    display: grid;
    grid-template-columns: 0.5fr 1fr 0.5fr;
    width: 50%;
    margin: 50px auto;
    text-align: center;
    align-items: center;
}
#blog-header > hr{
    width: 100%;
    height: min-content;
}
#blog-header > h3{
    font-size: 20px;
}
#blog-posts{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px;
    align-items: center;
    width: 75%;
    margin: auto;
}
.blog-posts-container > img{
    width: 100%;
}
.blog-posts-container > h3{
    font-size: 20px;
}
.blog-posts-container > p{
    font-size: 15px;
}